  .select-wrapper {
    display: flex;
    align-items: center;

    &::after {
      display: flex;
      align-items: center;
      font-family: 'Font Awesome 5 Free';
      content: "";
      position: absolute;
      right: 12px;
      height: calc(100% - 8px);
      padding-left: 12px;
      pointer-events: none;
      margin-top: 8px;
      font-weight: 900;
    }
  }

  .label_input {
    &__color {
      display: inline-flex;
      font-size: 14px;

      .color-swatch {
        width: 32px;
        height: 16px;
        margin-left: 12px;
      }
    }

    &__wrapper {
      position: relative;
    }

    &__append {
      position: absolute;
      right: 3px;
      top: 1px;
      padding: 10px;
      padding-bottom: 9px;
      font-size: 16px;
      color: var(--primary-text-color);
      font-family: inherit;
      pointer-events: none;
      cursor: default;
      max-width: 140px;
      white-space: nowrap;
      overflow: hidden;

      &::after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 1px;
        width: 5px;
        background-image: linear-gradient(to right, hsla(var(--background-color_hsl), 0), var(--background-color));
      }
    }
  }

  &__overlay-area {
    position: relative;

    &__overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: var(--background-color);
      backdrop-filter: blur(2px);
      border-radius: 4px;

      &__content {
        text-align: center;

        &.rich-formatting {
          &,
          p {
            color: var(--primary-text-color);
          }
        }
      }
    }
  }

  h2 {
    font-size: 20px;
    line-height: normal;
    margin-bottom: 14px;
    font-weight: bold;
  }

  .showable-password {
    position: relative;

    input {
      padding-right: 36px;
    }

    .icon-button {
      @apply text-black dark:text-white;
      position: absolute;
      top: 0;
      right: 0;
      height: 38px;
      width: 36px;
      padding: 0;
      margin: 0;
      background: transparent;

      .svg-icon {
        height: 20px;
        width: 20px;
      }
    }
  }
