.block-icon {
  display: block;
  margin: 0 auto;
  margin-bottom: 10px;
  font-size: 24px;
}

.simple_form {
  .warning {
    box-sizing: border-box;
    background: rgba($error-value-color, 0.5);
    color: var(--primary-text-color);
    text-shadow: 1px 1px 0 rgba($base-shadow-color, 0.3);
    box-shadow: 0 2px 6px rgba($base-shadow-color, 0.4);
    border-radius: 4px;
    padding: 10px;
    margin-bottom: 15px;

    a {
      color: var(--primary-text-color);
      text-decoration: underline;

      &:hover,
      &:focus,
      &:active {
        text-decoration: none;
      }
    }

    strong {
      font-weight: 600;
      display: block;
      margin-bottom: 5px;

      @each $lang in $cjk-langs {
        &:lang(#{$lang}) {
          font-weight: 700;
        }
      }

      .fa {
        font-weight: 400;
      }
    }
  }
}

.columns-area {
  form.simple_form--public {
    @include standard-panel;
    margin-top: 20px;
    padding: 60px 30px;
  }
}

.captcha {
  background-color: #fff;
  border-radius: 4px;

  img {
    display: table;
    margin: 0 auto;
  }

  input[type="text"] {
    border-radius: 0 0 4px 4px;
  }
}

.file-picker img {
  max-width: 100px;
  max-height: 100px;
}

.code-editor {
  textarea {
    font-family: monospace;
    white-space: pre;
  }

  &--invalid textarea {
    border-color: $error-red !important;
    color: $error-red;
  }

  .input {
    margin-bottom: 0;
  }

  .hint {
    margin-top: 10px;
  }
}

.delete-account {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

.input .row > .svg-icon.delete-field {
  height: 20px;
  width: 20px;
  position: absolute;
  right: 13px;
  cursor: pointer;
  color: $error-red;
  transform: translateY(-11px);
}

.input .row > .input.with_label + .svg-icon.delete-field {
  right: 5px;
  transform: translateY(7px);
}

.input.with_label.toggle .label_input {
  display: flex;
  font-size: 14px;
  align-items: center;

  .theme-toggle {
    margin-left: 10px;
  }
}

.actions.add-row {
  margin: 10px 0 0;

  .button {
    display: flex;
    align-items: center;
    border: 0;
    background: transparent;

    &:hover {
      color: var(--primary-text-color);
    }

    .svg-icon {
      height: 20px;
      width: 20px;
    }
  }
}

.copyable-input {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 38px;

  input {
    flex: 1;
    font-size: 14px !important;
    border-radius: 4px 0 0 4px !important;
    height: 100%;
  }

  button {
    width: auto;
    font-size: 14px;
    margin: 0;
    border-radius: 0 4px 4px 0;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
